import React, { Component } from 'react';
import api from '../utils/api.js';
import { Link } from 'react-router-dom';

export default class GeoLocation extends Component {
    constructor() {
        super();
        this.state = {
            city: ''
        }

    }
    componentWillMount() {
        if (navigator.geolocation) {
            navigator.geolocation.getCurrentPosition((position) => {

                api.getGeoLocation(position.coords.latitude, position.coords.longitude).then((response) => {
                    this.setState({
                        city: response.data.results[6].formatted_address
                    })
                    console.log(response.data.results[6].formatted_address)
                });

            })
        }
        else {
            console.log('Geolocation is not supported for this Browser/OS.');
        }
    }
    render() {
        return this.state.city == '' ?
            <div>
                <button className='button flex-column-center noClick'>
                    <div className="loader"></div>
                </button>
            </div>
            : <div className='button flex-column-center'>
                <Link className='geolocation-button' to={{
                    pathname: '/forecast',
                    search: '?city=' + this.state.city
                }} type='sumbit'>
                    @Your Location
                    
                </Link>
            </div>
        
    }
}



