import React, { Component } from 'react';
import queryString from 'query-string';
import api from '../utils/api';
import DayItem from './DayItem';
import WeekDayItem from './WeekDayItem';
import Clock from './Clock';
import moment from 'moment';
import ToggleButton from 'react-toggle-button'
import utils from '../utils/helpers';
import { Link } from 'react-router-dom'

var getDate = utils.getDate;


const C = ({ name }) => (
    <div>&#8451;</div>
);
const F = ({ name }) => (
    <div>&#8457;</div>
);



class Forecast extends Component {
    constructor(props) {
        super(props);
        this.state = {
            forecastDataToday: [],
            forecastDataUpcoming: [],
            loading: true,
            tempUnit: true
        }
    }
    componentDidMount() {
        this.city = queryString.parse(this.props.location.search).city;
        this.makeRequest(this.city);
    }
    componentWillReceiveProps(nextProps) {
        this.city = queryString.parse(nextProps.location.search).city;
        this.makeRequest(this.city);
    }
    makeRequest(city) {
        this.setState(function () {
            return {
                loading: true,
                error: false
            }
        })

        api.getWeather(city)
            .then((res) => {
                this.setState(function () {
                    return {
                        forecastDataToday: res[0].data,
                        forecastDataUpcoming: res[1].data,
                    }
                })
            }).catch((e) => {
                this.setState({loading:false, error: true});
            }).then(() => {
                if(this.state.error == true){
                    return;
                }
                api.getTimeLocation(this.state.forecastDataToday.coord.lat, this.state.forecastDataToday.coord.lon)
                .then((res) => {
                    this.setState({
                        loading: false,
                        timeZoneId: res.data.timeZoneId
                    })
                })
            });
    }
    handleClick(city) {
        city.city = this.city;
        this.props.history.push({
            pathname: '/details/' + this.city,
            state: city,
        })
    }
    render() {
        {{ console.log(this.state) }}
        if(this.state.loading == false && this.state.error == true){
            return ( 
                <div>
                    <div>
                        <Link to="/">&#8678;</Link>
                    </div>  
                    <div className='flex-column-center'>
                   <h1> No results </h1>
                    </div>
  
                </div>
            )
        } 
        return this.state.loading === true
            ? <h1 className='forecast-header flex-column-center'> Loading </h1>
            : <div className=''>
                <div className='forecast-header flex-column-center'>
                    <div>
                        <Link to="/">&#8678;</Link>
                    </div>
                    <div>
                        Local Time: <Clock time={this.state.timeZoneId}></Clock>
                    </div>

                    <ToggleButton
                        inactiveLabel={<F />}
                        activeLabel={<C />}
                        value={this.state.tempUnit || false}
                        onToggle={(value) => {
                            this.setState({
                                tempUnit: !value,
                            })
                        }} />
                </div>


                <DayItem day={this.state.forecastDataToday} temp={this.state.tempUnit}></DayItem>
                
                <div className='weekDayContainer'>
                    {this.state.forecastDataUpcoming.list.filter((day)=>{
                        return getDate(day.dt, 'ddd') !== getDate(this.state.forecastDataToday.dt, 'ddd') 
                    }).map((day, index) => {
                        return <WeekDayItem onClick={this.handleClick.bind(this, day)} day={day} temp={this.state.tempUnit} key={index}></WeekDayItem>
                    })}
                </div>
            </div>
    }
}

export default Forecast;



