var React = require('react');
var WeekDayItem = require('./WeekDayItem');
var utils = require('../utils/helpers');
var convertTempF = utils.convertTempF;

class Detail extends React.Component {
    constructor(props){
        super(props);

        this.state = {
            details: ''
        }
    }
    componentWillMount(){
        this.setState({ details: this.props.location.state})
    }
    render() {
        var props = this.props.location.state;
        var details = this.state.details;

        return !props
        ? <div> <a href='/'>Home</a></div>
        : <div className='detailsContainer'>
            <WeekDayItem day={props} />
            <div className='descriptionContainer'>
                <p>{details.city}</p>
                <p>{details.weather[0].description}</p>
                <p>min temp: {convertTempF(details.temp.min)} degrees</p>
                <p>max temp: {convertTempF(details.temp.max)} degrees</p>
                <p>humidity: {details.humidity}</p>
            </div>
        </div>
        
    }
}

module.exports = Detail;


