import GeoLocation from './GeoLocation';
import React, { Component } from 'react';

export default class Location extends Component {
    constructor(props) {
        super(props);
        this.state = {
            location: '',
        };

        this.handleSubmitLocation = this.handleSubmitLocation.bind(this);
        this.handleUpdateLocation = this.handleUpdateLocation.bind(this);
    }
    handleSubmitLocation() {
        this.props.onSubmitLocation(this.state.location)

        this.setState(function () {
            return {
                location: ''
            }
        })
    }
    handleUpdateLocation(e) {
        var zip = e.target.value;
        this.setState(function () {
            return {
                location: zip
            }
        });
    }
    render() {
        return (
            <div
                className='Location-container flex-column-center'
                style={{ flexDirection: this.props.direction }}>
                <input
                    className='form-control'
                    onChange={this.handleUpdateLocation}
                    placeholder='London, New York'
                    type='text'
                    value={this.state.Location} />
                <button
                    disabled={!this.state.location}
                    type='button'
                    style={{ margin: 10 }}
                    className='button'
                    onClick={this.handleSubmitLocation}>
                    Get Weather
                </button>

                <GeoLocation />
                
            </div>
        )
    }
}

