var React = require('react');
var utils = require('../utils/helpers');
var getFullDate = utils.getDate;
var convertTempF = utils.convertTempF;
var convertTempC = utils.convertTempC;


function DayItem(props) {
    var date = getFullDate(props.day.dt, 'LL');
    var icon = props.day.weather[0].icon;
    var tempF = convertTempF(props.day.main.temp);
    var tempC = convertTempC(props.day.main.temp);
    var city = props.day.name;
    return (
        <div onClick={props.onClick} className='dayContainer'>
        <div>
            <h1>{city}</h1>
            <h2 className=''>{date}</h2>
            {
                !props.temp ? 
                <span className='dayItemSubheader'>{tempF}&#8457;</span> : 
                <span className='dayItemSubheader'>{tempC}&#8451;</span>
            }

            <h2>{props.day.weather[0].description}</h2>
        </div>

        <div className='dayWeatherInfo'>
            <div className='dayWeatherInfoLeft'>
                <span>
                    <img className='weatherIcon' src={require('../images/weather-icons/' + icon + '.svg')} alt='Weather' />
                </span>
            </div>
        </div>


        </div>
    )
}

module.exports = DayItem;


