var React = require('react');
var utils = require('../utils/helpers');
var getDate = utils.getDate;
var convertTempF = utils.convertTempF;
var convertTempC = utils.convertTempC;


function DayItem(props) {
    var date = getDate(props.day.dt, 'dddd');
    var icon = props.day.weather[0].icon;
    var tempF = convertTempF(props.day.temp.day);
    var tempC = convertTempC(props.day.temp.day);


    return (
        <div onClick={props.onClick} className='weekDayItemContainer'>
            <div className=''>{date}</div>
            <img className='weather' src={require('../images/weather-icons/' + icon + '.svg')} alt='Weather' />
            
            {
                !props.temp ?
                    <div className=''>{tempF}&#8457;</div> :
                    <div className=''>{tempC}&#8451;</div>
            }


        </div>
    )
}

module.exports = DayItem;


